body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.header {
    color: white;
    width: 100%;
    background: #f33;
    padding: 1rem;
}

.header ul h1 {
    display: inline;
    font-size: 36pt;
}

.header ul a {
    text-decoration: none;
    color: inherit;
    font-size: 24pt;
}

.header ul li {
    display: inline;
    padding: 1rem;
}
.loginForm {
    text-align: center;
    border-style: solid;
    border-color: gray;
    background-color: lightgray;
    box-shadow: lime;
    margin: 3rem;
    width: auto;
}

.loginForm input{
    margin: 1rem .5rem;
    padding: .5rem 2rem;
}
